import { Component, Element, Event, EventEmitter, Host, Prop, Watch, h } from '@stencil/core';

@Component({
  tag: 'storm-multisteps-form',
  styleUrl: 'storm-multisteps-form.css',
  shadow: false,
})
export class StormMultistepsForm {
  @Element() host: HTMLElement;

  @Prop() stepTitle: string;
  @Prop() stepTitleDetail: string;
  @Prop() stepButton: string[] | string;
  @Event() formSubmit: EventEmitter;
  private _stepButton: string[];

  @Watch('stepButton')
  dataSetWatcher(newValue: string[] | string) {
    if (typeof newValue === 'string') {
      this._stepButton = JSON.parse(newValue);
    } else {
      this._stepButton = newValue;
    }
  }

  //DOM elements
  DOMstrings: {
    stepsBtnClass;
    stepsBtns;
    stepsBar;
    stepsForm;
    stepsFormTextareas;
    stepFormPanelClass;
    stepFormPanels;
    stepPrevBtnClass;
    stepNextBtnClass;
  };

  // @Method()
  // async resize() {
  //   this.setFormHeight()
  // }

  componentWillLoad() {
    if (!this.stepButton) return;
    this.dataSetWatcher(this.stepButton);
  }

  componentDidRender() {
    if (!this._stepButton && this.DOMstrings) return;
    this.DOMstrings = {
      stepsBtnClass: 'multisteps-form__progress-btn',
      stepsBtns: this.host.querySelectorAll(`.multisteps-form__progress-btn`),
      stepsBar: this.host.querySelector('.multisteps-form__progress'),
      stepsForm: this.host.querySelector('.multisteps-form__form'),
      stepsFormTextareas: this.host.querySelectorAll('.multisteps-form__textarea'),
      stepFormPanelClass: 'multisteps-form__panel',
      stepFormPanels: this.host.querySelectorAll('.multisteps-form__panel'),
      stepPrevBtnClass: 'js-btn-prev',
      stepNextBtnClass: 'js-btn-next',
    };
    //trigger stepbtn[0] by mao
    if (this.DOMstrings.stepsBtns.length > 0) this.DOMstrings.stepsBtns[0].click();

    // this.setFormHeight();

    //SETTING PROPER FORM HEIGHT ONLOAD
    // window.addEventListener('load', () => this.setFormHeight(), false);

    //SETTING PROPER FORM HEIGHT ONRESIZE
    // window.addEventListener('resize', () => this.setFormHeight(), false);
  }

  //remove class from a set of items
  removeClasses = (elemSet, className) => {
    elemSet.forEach(elem => {
      elem.classList.remove(className);
    });
  };

  //return exect parent node of the element
  findParent = (elem, parentClass) => {
    let currentNode = elem;

    while (!currentNode.classList.contains(parentClass)) {
      currentNode = currentNode.parentNode;
    }

    return currentNode;
  };

  //get active button step number
  getActiveStep = elem => {
    return Array.from(this.DOMstrings.stepsBtns).indexOf(elem);
  };

  //set all steps before clicked (and clicked too) to active
  setActiveStep = activeStepNum => {
    //remove active state from all the state
    this.removeClasses(this.DOMstrings.stepsBtns, 'js-active');

    //set picked items to active
    this.DOMstrings.stepsBtns.forEach((elem, index) => {
      if (index <= activeStepNum) {
        elem.classList.add('js-active');
      }
    });
  };

  //get active panel
  getActivePanel = () => {
    let activePanel;

    this.DOMstrings.stepFormPanels.forEach(elem => {
      if (elem.classList.contains('js-active')) {
        activePanel = elem;
      }
    });

    return activePanel;
  };

  //open active panel (and close unactive panels)
  setActivePanel = activePanelNum => {
    //remove active class from all the panels
    this.removeClasses(this.DOMstrings.stepFormPanels, 'js-active');

    //show active panel
    this.DOMstrings.stepFormPanels.forEach((elem, index) => {
      if (index === activePanelNum) {
        elem.classList.add('js-active');

        // this.setFormHeight(/*elem*/);
      }
    });
  };

  //從css設定，取消設定高度
  //set form height equal to current panel height
  // formHeight = activePanel => {

  //   const activePanelHeight = activePanel.offsetHeight;

  //   this.DOMstrings.stepsForm.style.height = `${activePanelHeight}px`;

  // };

  //從css設定，取消設定高度
  // setFormHeight = () => {
  //   const activePanel = this.getActivePanel();

  //   this.formHeight(activePanel);
  // };

  stepBarClick = async e => {
    //check if click target is a step button
    const eventTarget = e.target;

    if (!eventTarget.classList.contains(`${this.DOMstrings.stepsBtnClass}`)) {
      return;
    }

    //get active button step number
    const activeStep = this.getActiveStep(eventTarget);

    //set all steps before clicked (and clicked too) to active
    this.setActiveStep(activeStep);

    //open active panel
    this.setActivePanel(activeStep);
  };

  prevNextClick = async e => {
    const eventTarget = e.target;

    //check if we clicked on `PREV` or NEXT` buttons
    if (!(eventTarget.classList.contains(`${this.DOMstrings.stepPrevBtnClass}`) || eventTarget.classList.contains(`${this.DOMstrings.stepNextBtnClass}`))) {
      return;
    }

    //find active panel
    const activePanel = this.findParent(eventTarget, `${this.DOMstrings.stepFormPanelClass}`);

    let activePanelNum = Array.from(this.DOMstrings.stepFormPanels).indexOf(activePanel);

    //set active step and active panel onclick
    if (eventTarget.classList.contains(`${this.DOMstrings.stepPrevBtnClass}`)) {
      activePanelNum--;
    } else {
      activePanelNum++;
    }

    this.setActiveStep(activePanelNum);
    this.setActivePanel(activePanelNum);
  };

  sendClick = async (e: Event) => {
    e.stopPropagation();
    this.formSubmit.emit();
  };

  //add by mao
  setStepBtn = () => {
    return this._stepButton?.map(element => (
      <button class="multisteps-form__progress-btn" type="button" title={element} onClick={e => this.stepBarClick(e)}>
        {element}
      </button>
    ));
  };

  //add by mao
  setPanel = () => {
    return this._stepButton?.map(element => {
      let index = this._stepButton.indexOf(element);
      return (
        <div class="multisteps-form__panel border-radius-xl bg-white" data-animation="FadeIn">
          <div class="multisteps-form__content">
            <slot name={/*"multisteps"+*/ index.toString()}></slot>
            {this.setPrevNextSendBtn(index)}
          </div>
        </div>
      );
    });
  };

  //add by mao
  setPrevNextSendBtn = (index: Number) => {
    if (index == 0) {
      return (
        <div class="button-row d-flex mt-4">
          <button class="btn bg-gradient-dark ms-auto mb-0 js-btn-next" type="button" title="Next" onClick={e => this.prevNextClick(e)}>
            下一頁
          </button>
        </div>
      );
    } else if (this._stepButton.length - 1 == index) {
      return (
        <div class="button-row d-flex mt-0 mt-md-4">
          <button class="btn bg-gradient-light mb-0 js-btn-prev" type="button" title="Prev" onClick={e => this.prevNextClick(e)}>
            上一頁
          </button>
          <button class="btn bg-gradient-dark ms-auto mb-0" type="button" title="Send" onClick={e => this.sendClick(e)}>
            送出
          </button>
        </div>
      );
    } else {
      return (
        <div class="row">
          <div class="button-row d-flex mt-4 col-12">
            <button class="btn bg-gradient-light mb-0 js-btn-prev" type="button" title="Prev" onClick={e => this.prevNextClick(e)}>
              上一頁
            </button>
            <button class="btn bg-gradient-dark ms-auto mb-0 js-btn-next" type="button" title="Next" onClick={e => this.prevNextClick(e)}>
              下一頁
            </button>
          </div>
        </div>
      );
    }
  };

  render() {
    return (
      <Host>
        {/* <div class="container-fluid py-4"> */}
        {/* <div class="row min-vh-80"> */}
        <div class="row">
          {' '}
          {/* mark container and row add */}
          <div class="row">
            {' '}
            {/* progress bar contain */}
            <div class="col-12 col-lg-8 mx-auto my-5"></div>
          </div>
          <div class="col-lg-8 col-md-10 col-12 m-auto">
            {this.stepTitle ? <h3 class="mt-3 mb-0 text-center">{this.stepTitle}</h3> : ''}
            {this.stepTitleDetail ? <p class="lead font-weight-normal opacity-8 mb-7 text-center">{this.stepTitleDetail}</p> : ''}
            <div class="card">
              <div class="card-header p-0 position-relative mt-n5 mx-3 z-index-2">
                <div class="bg-gradient-primary shadow-primary border-radius-lg pt-4 pb-3">
                  <div class="multisteps-form__progress">{this.setStepBtn()}</div>
                </div>
              </div>
              <div class="card-body">
                <form class="multisteps-form__form">{this.setPanel()}</form>
              </div>
            </div>
          </div>
        </div>
        {/* </div> */}
        {/* </div> */}
      </Host>
    );
  }
}
